* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: #222831;
  border-radius: 5px;
}

body {
  background-color: #eeeeee;
}

.main-content {
  background-color: #393e46;
  box-shadow: black 5px 5px 7px;
}

.container {
  max-width: 750px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  padding: 30px;
}

.container h1 {
  margin: 0 auto;
  text-align: center;
}

.header,
.footer {
  border-radius: 5px;
  background-color: #eeeeee;
  box-shadow: black 5px 5px 7px;
  text-shadow: lightgrey 1px 1px 1px;
  text-align: center;
}

.footer {
  margin-top: 5em;
  font-size: 0.75em;
}

.header h2 {
  width: 100%;
  margin: 0 auto;
  font-style: italic;
  font-size: 1em;
}

.superhero-card-border {
  margin-top: 10px;
  margin-bottom: 10px;
  border: 1px solid #393e46;
  box-shadow: black 5px 5px 7px;
}

.superhero-card {
  background-color: #eeeeee;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 5px;
  box-shadow: black 5px 0px 7px;
}

.superhero-card:hover {
  cursor: pointer;
  opacity: 0.95;
}

.small-details {
  height: 3.25em;
}

.small-details h2 {
  display: inline;
}

img {
  box-shadow: black 1px 1px 3px;
}

.small-details img {
  float: left;
  margin-right: 1.5em;
}

.full-details img {
  margin: 0 auto;
  display: block;
}

.full-details h2 {
  text-align: center;
  text-shadow: lightgrey 1px 1px 1px;
}

.sub-title {
  font-style: italic;
  font-size: 0.75em;
}

.sub-details {
  width: 100%;
}

.sub-details th.sub-heading {
  text-align: center;
  border-bottom: 1px solid black;
  border-top: 1px solid black;
}

.sub-details th {
  text-align: left;
  border-bottom: 1px solid black;
  border-radius: 2px;
  border-right: 1px solid black;
}

td {
  width: 75%;
  border-bottom: 1px solid black;
  border-radius: 2px;
  font-weight: 500;
}

input {
  margin-top: 1em;
  width: 100%;
  height: 2.25em;
  padding: 1em;
}

.no-heroes-found {
  background-color: #eeeeee;
  margin: 10px;
  padding: 5px;
  text-align: center;
  box-shadow: black 5px 5px 7px;
}

table {
  margin: 2px;
  box-shadow: black 1px 1px 1px;
}

.filter-form {
  width: 100%;
  justify-content: center;
}

.filter-button {
  display: block;
  width: 85%;
  margin: 0.5em auto;
  padding: 0.1em;
}

.filter-button:hover {
  cursor: pointer;
}

.full-filter-form {
  background-color: #eeeeee;
  box-shadow: black 5px 5px 7px;
  padding: 5px;
}

label {
  padding: 3px;
}
